const openVideo = () => {
  document
    .querySelector(".about__right-play")
    .addEventListener("click", () => {
      fsLightboxInstances["video"].open(0);
    });
  document
    .querySelector(".item-play--1")
    .addEventListener("click", () => {
      fsLightboxInstances["video2"].open(0);
    });
  document
    .querySelector(".item-play--3")
    .addEventListener("click", () => {
      fsLightboxInstances["video3"].open(0);
    });
}


export default openVideo;
