const smoothScroll = () => {
  const menuBtnAll = document.querySelectorAll(".nav__link");
  const menuBtnAllTablet = document.querySelectorAll(
    ".header-tablet__nav-link"
  );
  const btnScrollTop = document.querySelector(".btn-top");
  const menu = document.querySelector(".header-tablet__bottom");
  const burger = document.querySelector(".burger");
  const heroBtnScroll = document.querySelector(".hero__btn");

  btnScrollTop.style.display = "none";

  heroBtnScroll.addEventListener("click", (event) => {
    event.preventDefault();

    const section = document.querySelector(heroBtnScroll.getAttribute("href"));

    if (section) {
      section.scrollIntoView({
        behavior: "smooth",
        block: "start",
        inline: "center",
      });
    }
  });

  menuBtnAll.forEach((elem) => {
    elem.addEventListener("click", (event) => {
      event.preventDefault();

      const section = document.querySelector(elem.getAttribute("href"));

      if (section) {
        section.scrollIntoView({
          behavior: "smooth",
          block: "start",
          inline: "center",
        });
      }
    });
  });

  menuBtnAllTablet.forEach((elem) => {
    elem.addEventListener("click", (event) => {
      event.preventDefault();

      const section = document.querySelector(elem.getAttribute("href"));

      menu.classList.toggle("header-tablet__bottom--active");

      burger.classList.toggle("burger--active");

      if (section) {
        section.scrollIntoView({
          behavior: "smooth",
          block: "start",
          inline: "center",
        });
      }
    });
  });

  window.addEventListener("scroll", () => {
    let scrennHeight = document.documentElement.clientHeight;
    let coords = window.pageYOffset;

    if (scrennHeight > coords) {
      btnScrollTop.style.display = "none";
    } else if (scrennHeight < coords) {
      btnScrollTop.style.display = "flex";
    }
  });

  btnScrollTop.addEventListener("click", () => {
    const html = document.querySelector("html");
    html.style.scrollBehavior = "smooth";
    document.documentElement.scrollTop = 0;
  });
};

export default smoothScroll;
